.image-modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,.8);display:flex;align-items:center;justify-content:center;z-index:1000}.image-modal .modal-content{position:relative;max-width:90%;max-height:90%;overflow:hidden}.image-modal .modal-content img{max-width:100%;max-height:100%}.image-modal .close-btn{position:absolute;top:10px;right:15px;font-size:32px;color:#fff;cursor:pointer}